@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 20px;
}

body {
  font-family: 'Poppins', sans-serif;
}

select, input {
  font-family: inherit;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 12px;
}

/* TODO: Distribute styles */

input, select {
  padding: 6px;
  font-family: inherit;
  font-size: inherit;
}

td, th {
  border: 1px solid lightgray;
  padding: 6px;
  min-width: 50px;
  text-align: center;
}

th:first-child {
  text-align: right;
}

td.highlighted {
  background-color: lightgreen;
}

select.big {
  height: 100%;
  min-width: 120px;
  font-size: 3rem;
  text-align: center;
}

input[type="number"] {
  width: 80px;
}

